#wrap.mobile{
    width: 100%;
    max-width: 1024px;
    margin: 0 auto;
}

#wrap.mobile #main {
    position: absolute;
    top: 0px;
    width: 100%;
    max-width: 1024px;
}




/******** web page***********/
#Sec0_web {
    width: 100%;
    padding-bottom: 100px;
}



#Sec0_web .sect-inner {
    width: 80%;
    position: relative;
    margin: 0 auto;
    }
    
#Sec0_web .sect-inner .sect-title{
    text-align: center;
    font-size: 80px;
    font-family: 'Bebas Neue', cursive;
    padding-bottom: 20px;
    padding-top: 80px;
    color: #acacac;
    }

#Sec0_web .sect-inner .sect-content .screen-wrap{
    position: relative;
    width: 100%;
    height: 100%;
}
#Sec0_web .sect-inner .sect-content{
    height: 740px;
    position: relative;
}

#Sec0_web .sect-inner .sect-content .screen-wrap .screen{
    position: absolute;   
}

#Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{
    width: 60%; right: 30%; bottom: 0;
    
}
#Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{
    width: 40%; right: 17%; bottom: 0;
    
}
#Sec0_web .sect-inner .sect-content .screen-wrap .screen.mobile{
    width: 80px; right: 12%; bottom: 0;
    
}


#Sec0_web .sect-inner .sect-content .work-text{
    position: absolute; top: 5%; left: 0; width: 100%;
    padding-bottom: 20px;
    
}
#Sec0_web .sect-inner .sect-content .work-text h4{
    margin-bottom: 12px; margin-top: 30px; font-size: 36px;
    text-align: center;
}
#Sec0_web .sect-inner .sect-content .work-text .desc{
    margin-bottom: 12px;
}
#Sec0_web .sect-inner .sect-content .work-text .list{
    display: flex; padding: 20px 0;
}
#Sec0_web .sect-inner .sect-content .work-text .list li{
    text-align: center;
    word-break: keep-all; flex: 1;
    padding: 2px 0; font-weight: 500; color: #555; font-size: 14px;
}
#Sec0_web .sect-inner .sect-content .work-text .list li:not(:first-child){
    margin-left: 18px;
}

#Sec0_web .sect-inner .sect-content .work-text .link-wrap{
    margin: 0 auto; margin-top: 18px; text-align: center;
}

#Sec0_web .sect-inner .sect-content .work-text .link-wrap button{
    padding: 12px 24px;
    border: 1px solid #ddd;
}
#Sec0_web .sect-inner .sect-content .work-text .link-wrap button:nth-child(1){
    margin-right: 14px;
}



@media screen and (max-width:950px){
    
   
}


@media screen and (max-width:768px){
    #Sec0_web .sect-inner{width: 90%;}
    #Sec0_web .sect-inner .sect-content .work-text{width: 100%;}
   
    /* #Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{width: 60%;}
    */
    /* #Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{width: 55%;} */
    #Sec0_web .sect-inner .sect-content .work-text .link-wrap button{display: inline-block;}
}

@media screen and (max-width:600px){
    #Sec0_web .sect-inner .sect-content .work-text{top: 4%;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{width: 72%;}
   
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{display: none;}
   
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.mobile{display: none;}
    #Sec0_web .sect-inner .sect-content{height: 720px;}
    #Sec0_web .sect-inner .sect-content .work-text .list{display: block;}
    #Sec0_web .sect-inner .sect-content .work-text .list li{
        margin-bottom: 10px; background: #eee;
    }
    #Sec0_web .sect-inner .sect-content .work-text .list li:not(:first-child){
        margin-left: 0;
    }

    
}

@media screen and (max-width:480px){
    #Sec0_web .sect-inner .sect-title{font-size: 52px;}
    #Sec0_web .sect-inner .sect-content .screen-wrap{margin-bottom: 40px;}
    #Sec0_web .sect-inner{margin: 0 16px; width: calc(100% - 32px);}
    #Sec0_web .sect-inner .sect-content{height: 750px; text-align: center;}
    #Sec0_web .sect-inner .sect-content .work-text {width: 100%;}
    #Sec0_web .sect-inner .sect-content .work-text .desc{font-size: 14px;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen img{width: 100%;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.pc{right: 50%; transform: translateX(50%);}
    #Sec0_web .sect-inner .sect-content .work-text .link-wrap{margin-top: 0;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.tablet{display: none;}
    #Sec0_web .sect-inner .sect-content .screen-wrap .screen.mobile{display: none;}
   

}